овки и увеличению расходов на дезинсекцию. ")]),_c('h3',[_vm._v("КОМУ НЕОБХОДИМО ПРОВЕДЕНИЕ ДЕЗИНСЕКЦИИ ПОМЕЩЕНИЙ?")]),_c('p',[_vm._v(" Дезинсекция дачи, частного дома, участка либо коммерческого, торгового, производственного объекта имеет только одну цель — избавление от насекомых. Нередко в рамках мероприятий по борьбе с вредителями дополнительно проводится дезинфекция — в этом случае речь идет уже об обеззараживании помещений после применения ядохимикатов, позволяющих буквально за один визит специалистов вернуть жилищу нормальные санитарно-гигиенические показатели. ")]),_c('p',[_vm._v(" К услугам дезинсекторов хотя бы раз в жизни прибегает каждый человек. И это вполне оправданный подход. Ведь только специалисты способны обеспечить эффективную борьбу с вредителями. ")]),_c('h3',[_vm._v("МЕРЫ ЭКСТРЕННОЙ ОБРАБОТКИ ПОМЕЩЕНИЙ")]),_c('p',[_vm._v(" Истребительная дезинсекция — этап, на котором и производится уничтожение насекомых. Этот вид мероприятий предполагает очаговую (локальную) обработку — например, в многоквартирном доме это отдельная квартира. А на участке — обработка от тли или муравьев, не нарушающая нормальное состояние биосферы и безопасная для полезных насекомых и птиц. Применяются экстренные меры воздействия и в том случае, если нашествию насекомых неожиданно подвергаются предприятия и организации, работающие на государственной или частной основе. ")]),_c('h1',[_vm._v("PHOTO")]),_c('h3',[_vm._v("ПРИМЕНЯЕМЫЕ МЕТОДЫ ПРОФЕССИОНАЛЬНОЙ ДЕЗИНСЕКЦИИ")]),_c('p',[_vm._v(" Среди существующих методов борьбы с вредоносными насекомыми существует несколько наиболее популярных вариантов — именно их используют чаще других. К таковым относят: ")]),_vm._m(7),_c('h3',[_vm._v("ДЕЗИНСЕКЦИЯ ПОМЕЩЕНИЙ ДЛЯ ОРГАНИЗАЦИЙ")]),_c('p',[_vm._v(" Для производственных и промышленных объектов наша санитарная служба, выезжающая для проведения мероприятий по всей территории Москвы и Московской области, обычно разрабатывает план профилактических мероприятий, позволяющих провести дезинсекцию 